.project-heading {
    text-align: center;
    padding: 5rem 2rem 2rem 2rem;
    font-size: 2.5rem;
    color: #f8fafc;
    font-weight: 800;
    background: linear-gradient(135deg, #4a90e2 0%, #7c3aed 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.work-container {
    width: 100%;
    padding: 3rem 0 5rem 0;
    background: linear-gradient(135deg, rgba(10, 14, 39, 0.9) 0%, rgba(22, 33, 62, 0.8) 50%, rgba(15, 52, 96, 0.7) 100%);
    position: relative;
    overflow: hidden;
}

.work-container::before {
    content: '';
    position: absolute;
    top: -50%;
    right: -50%;
    width: 600px;
    height: 600px;
    background: radial-gradient(circle, rgba(74, 144, 226, 0.1), transparent);
    border-radius: 50%;
    animation: float 20s ease-in-out infinite;
}

@keyframes float {
    0%, 100% { transform: translateY(0px) translateX(0px); }
    50% { transform: translateY(20px) translateX(20px); }
}

.project-container {
    max-width: 1200px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    padding: 0 2rem 5rem 2rem;
    grid-gap: 2rem;
}

.project-card {
    background: linear-gradient(135deg, rgba(10, 14, 39, 0.8) 0%, rgba(30, 41, 59, 0.95) 100%);
    padding: 2rem;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(74, 144, 226, 0.3);
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    transition: all 0.4s cubic-bezier(0.23, 1, 0.320, 1);
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(10px);
}

.project-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(74, 144, 226, 0.1), transparent);
    transition: left 0.5s ease;
}

.project-card:hover::before {
    left: 100%;
}

.project-card:hover {
    background: linear-gradient(135deg, rgba(20, 30, 50, 0.95) 0%, rgba(40, 50, 70, 1) 100%);
    transform: translateY(-15px) scale(1.02);
    border-color: rgba(74, 144, 226, 0.8);
    box-shadow: 0 30px 60px rgba(74, 144, 226, 0.25), 0 0 40px rgba(124, 58, 237, 0.15);
}

.project-card img {
    width: 100%;
    height: 240px;
    object-fit: cover;
    border-radius: 12px;
    transition: all 0.4s ease;
    filter: brightness(0.9) contrast(1.1);
}

.project-card:hover img {
    transform: scale(1.1) rotate(1deg);
    filter: brightness(1.1) contrast(1.2);
}

.project-title {
    color: #f8fafc;
    font-size: 1.5rem;
    font-weight: 700;
    text-align: left;
    margin: 0.5rem 0;
    line-height: 1.4;
    background: linear-gradient(135deg, #ffffff 0%, #cbd5f5 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.pro-details p {
    margin: 0.8rem 0;
    font-size: 1rem;
    color: #cbd5e1;
    line-height: 1.7;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.pro-details h3 {
    display: block;
    margin: 1.2rem 0 1rem 0;
    color: #94a3b8;
    font-weight: 500;
    font-size: 0.95rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.techstack {
    flex-wrap: wrap;
    gap: 0.5rem;
}

.pro-btn {
    display: flex;
    gap: 1rem;
    margin-top: auto;
    padding-top: 1rem;
}

.pro-btn a {
    text-decoration: none;
    color: #f8fafc;
    background: linear-gradient(135deg, #4a90e2 0%, #357abd 100%);
    border: 2px solid #4a90e2;
    padding: 0.8rem 1.8rem;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    transition: all 0.3s ease;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.pro-btn a:hover {
    background: linear-gradient(135deg, #357abd 0%, #2d5a9e 100%);
    border-color: #2d5a9e;
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(74, 144, 226, 0.3);
}

@media screen and (max-width: 1024px) {
    .project-container {
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
        padding: 0 1.5rem 4rem 1.5rem;
    }
}

@media screen and (max-width: 740px) {
    .project-container {
        max-width: 100%;
        padding: 0 1rem 3rem 1rem;
        grid-template-columns: 1fr;
    }

    .project-heading {
        padding: 3rem 1rem 1.5rem 1rem;
        font-size: 2rem;
    }

    .project-card {
        padding: 1.4rem;
    }

    .project-card img {
        height: 200px;
    }

    .project-title {
        font-size: 1.2rem;
    }

    .pro-details p {
        font-size: 0.95rem;
    }

    .pro-btn {
        flex-direction: column;
    }

    .pro-btn a {
        width: 100%;
        text-align: center;
    }
}
